
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { PopupModalModule } from 'app/shared/modal/popup-modal.module';
import { ModalModule } from 'ng2-bootstrap/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { InfoNutricionalPopupModalComponent } from './popup/unidade-medida-popup-modal.component';
import { InfoNutricionalService } from './info-nutricional.service';
import { InfoNutricionalRoutingModule } from './info-nutricional-routing.module';
import { ListarInfoNutricionalComponent } from './listar/listar-info-nutricional.component';
import { SharedComponentModule } from './../../../shared/shared-component.module';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    InfoNutricionalRoutingModule
  ],
  declarations: [
    ListarInfoNutricionalComponent,
    InfoNutricionalPopupModalComponent
  ],
  providers:[
    InfoNutricionalService
  ]
})
export class InfoNutricionalModule { }



