import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { InfoNutricionalService } from './../info-nutricional.service';
import { InfoNutricionalDTO } from 'models/domain/info-nutricional.dto';

@Component({
  selector: 'info-nutricional-popup-modal',
  templateUrl: './unidade-medida-popup-modal.component.html',
  styleUrls: ['./unidade-medida-popup-modal.component.scss']

})
export class InfoNutricionalPopupModalComponent extends AbstractBaseComponent implements OnInit {

  titulo: string = "Confirmar";
  item: InfoNutricionalDTO;

  @ViewChild('modalConfirm') confirma;
  @ViewChild('campoNome') campo;

  @Output() onClickConfirm = new EventEmitter<any>();

  constructor(
    public fb: FormBuilder) {
    super();
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.titulo = "Cadastra informação nutricional"
    this.criarForm();
    this.confirma.show();
    this.fieldFocus(this.campo);
  }

  showAlteracao(item: InfoNutricionalDTO) {
    this.titulo = "Alterar informação nutricional"
    this.alterarForm(item);
    this.confirma.show();
    this.fieldFocus(this.campo);
  }

  btnConfirmar(item: InfoNutricionalDTO) {
    this.onClickConfirm.emit(item);
    this.confirma.hide();
  }

  criarForm() {
    this.formulario = this.fb.group({
      id: [''],
      nome: ['', Validators.required],
      emAlteracao: [false]
    })
  }

  alterarForm(item: InfoNutricionalDTO) {
    this.formulario.patchValue({
      id: item.id,
      nome: item.nome,
      emAlteracao: true
    })
  }

}



