import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { create } from 'domain';
import { ListarInfoNutricionalComponent } from './listar/listar-info-nutricional.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Informações nutricional'
    },
    children: [
      {
        path: 'listar',
        component: ListarInfoNutricionalComponent,
        data: {
          title: 'Listar'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InfoNutricionalRoutingModule { }



